<template>
  <div class="message-container">
    <dl class="users-list">
      <dt class="users-title">
        <span class="title"></span>
        <span class="num">{{unreadCount}}</span>
      </dt>
      <van-list
        v-model="isLoading"
        :finished="finished"
        finished-text=""
        @load="fetchMessage"
        class="person-list"
      >
        <dd class="users-item" v-for="(item, index) in list" :key="index" @click="jump(item.id)">
          <div class="head-box">
            <div class="img-box">
              <img :src="`/api/member/pic?id=${item.coverPicList?item.coverPicList[0]:0}&type=1&targetMemberId=${item.id}`" alt="" class="img" />
            </div>
            <div v-show="item.status == 0">
              <span class="sentence-num"></span>
            </div>
          </div>
          <div class="infomation" @click="jump(item.fromMemberId, item.id, index)">
            <p class="name-box"><span class="name">{{item.name}}</span></p>
            <p class="sentence">{{item.message}}</p>
          </div>
        </dd>
      </van-list>
    </dl>

    <van-dialog v-model="isShowDialog" :confirmButtonText="dialogConfirmText" @confirm="confirm" @closed="closed">
      <div class="user-message-dialog">
        <p class="tips">ta</p>
        <div class="not-show-again" @click="checkKnown">
          <span class="check-box" :class="{'isChecked': notShowAgain}"></span>
          <span class="known"></span>
        </div>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import Storage from '@/utils/Storage'
export default {
  name: 'account',
  data () {
    return {
      list: [],
      currentId: undefined,
      isShowDialog: false,
      dialogConfirmText: '',
      notShowAgain: false,
      unWatcher: null,
      currentPage: 1,
      pageSize: 20,
      isLoading: false,
      finished: false
    }
  },
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapState('message', {
      unreadCount: 'unreadCount'
    }),
    ...mapGetters({
      userId: 'userId'
    })
  },
  methods: {
    ...mapMutations('message', {
      updateUnreadCount: 'updateUnreadCount'
    }),
    jump (userId, id, index) {
      this.$root.openLoading()
      this.$http('/api/member/message/read', {
        currentMemberId: this.userId,
        messageId: id
      }, 'get').then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.list[index].status = 1
          if (this.unreadCount > 0) {
            this.updateUnreadCount(this.unreadCount - 1)
          }
          this.$router.push({
            path: `/message-detail-${userId}.html`
          })
        } else {
          this.$toast(res.msg)
        }
        this.isLoading = false
      }, err => {
        this.isLoading = false
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    toDetail () {
      this.$router.push({
        path: `/info-${this.currentId}.html`
      })
    },
    popup (id) {
      this.currentId = id
      if (!Storage.load('neverPopMessage')) {
        this.isShowDialog = true
        return
      }

      // 
      this.toDetail()
    },
    checkKnown () {
      this.notShowAgain = !this.notShowAgain
    },
    confirm () {
      if (this.notShowAgain) {
        Storage.save('neverPopMessage', true)
      } else {
        Storage.save('neverPopMessage', false)
      }
    },
    closed () {
      // 
      this.toDetail()
    },
    fetchMessage () {
      this.isLoading = true
      this.$http('/api/member/message/list', {
        currentMemberId: this.userId,
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }, 'get').then(res => {
        this.isLoading = false
        if (res.code === '0000') {
          this.list = res.data.data
          if (this.currentPage === res.data.pageCount) {
            this.finished = true
          } else {
            this.finished = false
          }
          this.currentPage++
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.isLoading = false
        this.$toast(err)
      })
    }
  },
  activated () {
    this.unWatcher = this.$watch('unreadCount', (val) => {
      if (parseInt(val) >= 0) {
        this.currentPage = 1
        this.fetchMessage()
      }
    })
  },
  deactivated () {
    this.unWatcher && this.unWatcher()
  },
  mounted () {
    this.fetchMessage()
  }
}
</script>

<style lang="scss">
  @import "@/assets/scss/constant.scss";
  .message-container {
    .user-message-dialog {
      padding: 40px 28px 40px 36px;
      .tips {
        color: #9B9BA1;
        font-size: 28px;
        line-height: 40px;
      }
      .not-show-again {
        margin-top: 40px;
      }
      .check-box {
        display: inline-block;
        vertical-align: middle;
        width: 40px;
        height: 40px;
        border-radius: 2px;
        border: 1px solid #979797;
      }
      .known {
        display: inline-block;
        vertical-align: middle;
        margin-left: 10px;
        color: #9B9BA1;
        font-size: 28px;
        line-height: 40px;
      }
    }
  }
</style>
