<script>
import { mapState, mapGetters, mapMutations } from 'vuex'

import MyHeader from '@/components/common/myHeader'
import purchaseModal from '@/components/common/purchaseModal'
import { copy } from '@/utils/tools'
import Storage from '@/utils/Storage'
import wxApi from '@/utils/wxAuth'
import { Toast } from 'vant'

export default {
  name: 'account',
  components: {
    MyHeader,
    purchaseModal
  },
  data () {
    return {
      isShowModal: false,
      showConditionPop: false,
      showEduPicker: false,
      // 
      showStatusPicker: false,
      // statusColumns: ['', '', '', '', '', '', ''],
      areaColumns: 2,
      isShowAreaPicker: false,
      isForeign: false,
      // 
      filterData: {
        selectionAgeMin: '',
        selectionAgeMax: '',
        selectionHighMin: '',
        selectionHighMax: '',
        selectionAcadmic: 0,
        selectionCityId: '',
        selectCountry: '',
        selectState: ''
      },
      showAuthDialog: false,
      authDialogMessage: '',
      path: ''
    }
  },
  computed: {
    ...mapState({
      userData: 'userData',
      educationMap: 'educationMap',
      statusMap: 'statusMap',
      areaData: 'areaData'
    }),
    ...mapGetters({
      userId: 'userId'
    }),
    isEditableCert () {
      // 
      return parseInt(this.userData.basedataAuth) === 3
    },
    EduColumns () {
      let arr = []
      for (let i in this.educationMap) {
        arr.push({
          value: i,
          text: this.educationMap[i]
        })
      }
      return arr
    },
    statusColumns () {
      let arr = []
      for (let i in this.statusMap) {
        arr.push({
          value: i,
          text: this.statusMap[i]
        })
      }
      return arr
    }
  },
  watch: {
    userData (val) {
      if (this.userId && val.status === 0) {
        return this.$router.push('/setUserData.html')
      }
      if (this.userId) {
        let filterData = this.filterData
        let userData = this.userData
        filterData.selectionAgeMin = userData.selectionAgeMin
        filterData.selectionAgeMax = userData.selectionAgeMax
        filterData.selectionAcadmic = userData.selectionAcadmic
        filterData.selectionCityId = userData.selectionCityId
        filterData.selectionHighMax = userData.selectionHighMax
        filterData.selectionHighMin = userData.selectionHighMin
        filterData.selectionSingleStatus = userData.selectionSingleStatus
        if (userData.selectionCityId === 90000) {
          this.areaColumns = 1
        } else {
          this.areaColumns = 2
        }
      }
    }
  },
  methods: {
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    // code 
    async getToken (authorCode) {
      try {
        // authorCode = '0716evS72mB6GL086KQ72cZMS726evS1'
        let { code, data, msg } = await this.$http('/api/login/wx/accesstoken', { code: authorCode || '' }, 'get')
        if (!(code === '0000' && data)) {
          return Toast(msg)
        }
        localStorage.setItem('token', data.accessToken || '')
        Storage.save('userInfo', JSON.stringify(data))
        Storage.save('userId', data.currentMemberId)
        this.updateUserData(data)
        if (data.status === 0) {
          this.$router.push('setUserData.html')
        }
      } catch (error) {
        Toast(error)
      }
    },
    wxAuthor () {
      if (this.userId) {
        return
      }
      wxApi.init({
        appid: 'wx2bdc6bb81e3f570e', // 
        redirectUri: location.href
      }).author()
    },
    transferStatus (status) {
      let c = {}
      switch (parseInt(status)) {
        case 0:
        case 4:
          c.class = 'static'
          c.text = ''
          break
        case 1:
          c.class = 'emphasize'
          c.text = ''
          break
        case 2:
          c.class = 'emphasize'
          c.text = ''
          break
        case 3:
          c.class = 'emphasize'
          c.text = ''
          break
      }
      return c
    },
    execCopy (text) {
      copy(text, this.$refs['code-con']).then(res => {
        this.$toast('')
      }, () => {
        this.$toast('')
      })
    },
    purchase () {
      this.isShowModal = true
    },
    async purchaseLikeNum (selectKey) {
      let fd = new FormData()
      fd.append('productId', selectKey)
      this.$root.openLoading()
      try {
        let { code, data, msg } = await this.$http('/api/member/purchase/transaction', fd)
        if (!(code === '0000' && data)) {
          this.$root.closeLoading()
          return this.$toast(msg)
        }
        // 
        this.$http('/api/login/wx/jssdk/config/get', {
          currentMemberId: this.userId || 0,
          pageUrl: location.href
        }, 'get').then(res => {
          this.$root.closeLoading()
          if (res.code === '0000') {
            wxApi.init({
              appid: data.appId, // 
              redirectUri: location.href
            }).wexinPay(data, res.data).then(res => {
              let msg = res.actionCode === 200 ? '' : ''
              this.$toast(msg)
            }).catch(e => {
              this.$toast('')
            })
          }
        }, err => {
          this.$root.closeLoading()
          this.$toast(err)
        })
      } catch (err) {
        this.$root.closeLoading()
        this.$toast(err)
      }
      this.isShowModal = false
    },
    toBaseInfo () {
      this.$router.push('/baseInfo.html')
    },
    switchConditionPop (e) {
      e && e.preventDefault()
      this.showConditionPop = !this.showConditionPop
    },
    onConfirmEdu (val) {
      this.filterData.selectionAcadmic = val.value
      this.showEduPicker = false
    },
    onConfirmStatus (val) {
      this.filterData.selectionSingleStatus = val.value
      this.showStatusPicker = false
    },
    resetCondition () {
      this.filterData = {
        selectionAgeMin: '',
        selectionAgeMax: '',
        selectionHighMin: '',
        selectionHighMax: '',
        selectionAcadmic: 0,
        selectionCityId: '',
        selectCountry: '',
        selectState: '',
        selectionSingleStatus: null
      }
    },
    uploadCondition () {
      if (this.filterData.selectionAgeMin > this.filterData.selectionAgeMax) {
        this.$toast('')
        return
      }
      if (this.filterData.selectionHighMin > this.filterData.selectionHighMax) {
        this.$toast('')
        return
      }
      this.$root.openLoading()
      this.$http('/api/member/save', {
        ...this.filterData,
        currentMemberId: this.userId
      }).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.updateUserData(this.filterData)
        } else {
          Toast(res.msg)
        }
      }).catch(err => {
        this.$toast(err)
        this.$root.closeLoading()
      })
      this.switchConditionPop()
    },
    jumpTo (type) {
      if (this.isEditableCert) {
        switch (type) {
          case 'id':
            this.authDialogMessage = this.userData.identityAuth ? '' : ''
            this.path = '/upload-cert.html'
            break
          case 'education':
            this.authDialogMessage = this.userData.academicAuth ? '' : ''
            this.path = '/upload-education.html'
            break
          case 'career':
            this.authDialogMessage = this.userData.workAuth ? '' : ''
            this.path = '/upload-career.html'
            break
        }
      } else {
        // 
        this.authDialogMessage = ''
      }
      if (this.authDialogMessage) {
        this.showAuthDialog = true
      } else {
        this.jump()
      }
    },
    jump () {
      this.$router.push(this.path)
    },
    switchAreaPicker () {
      this.isShowAreaPicker = !this.isShowAreaPicker
    },
    handleAreacancel () {
      this.isShowAreaPicker = false
    },
    handleAreachange (picker, res) {
      if (res[0].code === '900000') {
        this.areaColumns = 1
      } else {
        this.areaColumns = 2
      }
    },
    handleAreaconfirm (res) {
      this.isShowAreaPicker = false
      // 
      if (res[0].code === '900000') {
        this.isForeign = true
        this.filterData.selectionCityId = res[0].code
      } else {
        this.isForeign = false
        this.filterData.selectionCityId = res[1].code
      }
    }
  },
  created () {
    console.error('wx callBack===>', window.location.href)
    //  code,
    if (wxApi.getUrlParams().code !== undefined) {
      this.getToken(wxApi.getUrlParams().code)
    }
    // this.$toast('')
  }
}
</script>

